.main-header{
    background: var(--header-bgColor-lm);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 5px;
    position: sticky;
    top: 0;

    .header-content {
        display: flex;
        align-items: center;
        gap: 40px;
    }

    .company-container {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            height: 60px;
            width: 60px;
        }

        span {
            font-size: 18px;
        }
    }
}

/* ---------ICONS--------- */
.icons__div {
    display: flex;
    gap:15px;
    cursor: pointer;
}

.icon__div {
    color: white;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    transition: transform 0.2s linear;
}

.icon__div:hover {
    color: orange;
    transform: translateY(-0.3rem);
}

.icon__div .bi {
    background-color: transparent;
    height: 35px;
    width: 35px;
}

/* --------- NOTIFICATION --------- */
.notification__div {
    position: absolute;
    top: 10px;
    left: 25px;
}

.notification__number {
    font-size: 10px;
    color: white;
}

button.notification__button {
    background-color:red;
    height: 20px;
    width: 20px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    padding: 5px;
}

