.company_div{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;

    img{
        border-radius: 9px;
        width: 60px;
        height: 60px;
    }

    h1{
        font-size: 25px;
    }
}


/* FOOTER MENU */
.menus{
    height: fit-content;
    background-color: #00284c;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px 40px;
    padding: 25px;

    .menu{
        display: flex;
        align-items: start;
        flex-direction: column;
        gap: 0 10px;
        height: fit-content;
        width: 120px;

        h2{
            font-size: 25px;
            color: gold;
        }
        ul {
            padding: 0;
            display: flex;
            flex-direction: column;
            align-items: start;
            justify-content: start;
            gap: 8px;

            li{
                list-style: none;
                a{
                    text-decoration: none;
                    color: white;
                    font-size: 20px;
                }
                a:hover{
                    color: gold;
                }
            }

            li:hover{
                transform: scale(0.95);
            }
        }
    }
}


.Newsletter{
    background-color: #004077;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    h1{
        text-align: center;
        font-size: 16px;
    }

    .newsletter__div{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
    
        input{
            height: 50px;
            width: 250px;
            background-color: rgb(213, 213, 213);
            color: black;
            border: none;
            border-radius: 4px;
            padding-left: 5px;
        }
    
        input:hover{
            background-color: rgb(255, 255, 255);
        }
    
        input::placeholder{
            color: grey;
        
        }
        
        button{
            height: 50px;
            width: 50px;
            background-color: rgb(246, 201, 0);
            color: black;
            border: none;
            border-radius: 4px;
            transition: all 0.3s ease-in-out;
        }
    
        button:hover{
            background-color: rgb(0, 0, 0);
            color: white;
        }
    
        input:focus,
        input:active{
            outline: none;
        }
    }
}



.Download-Store {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    img{
        height: 50px;
        width: 100px;
        border: 2px black solid;
        cursor: pointer;
    }
}

/* -----------------------REACH US----------------------- */
.reach_us__div{
    background-color: #00284c;
    color: white;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;

    .contact{
        width: 150px;
        height: fit-content;
        background-color: rgb(0, 128, 255);
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 10px 0;
        gap: 5px;
        border-radius: 9px;
        cursor: pointer;
        transition: all 0.5s linear;
        h2{
            font-size: 12px;
        }
        
        svg{
            width: 25px;
            height: 25px;
        }
    }

    .contact:hover{
        background-color: gold;
        color: black;
    }

}



/* COPYRIGHT DIV */
.copyright_div{
    height: fit-content;
    width: 100%;
    padding: 20px 0;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}